'use client';
import React from 'react';
import { KameleoonClient } from '@kameleoon/javascript-sdk';
import { KameleoonContext } from './KameleoonContext';

export interface IKameleoonProviderProps {
  /** Child elements of the provider. */
  children: React.ReactNode;
  /** An instance of Kameleoon SDK. */
  client: KameleoonClient;
}

/**
 * A Provider that wraps the project and provides an access to SDK APIs
 */
function KameleoonProvider({
  client,
  children,
}: IKameleoonProviderProps): React.JSX.Element {
  return (
    <KameleoonContext.Provider
      value={{
        kameleoonClient: client,
        sdkParameters: null,
      }}
    >
      {children}
    </KameleoonContext.Provider>
  );
}

export default React.memo(KameleoonProvider);
