'use client';
import { useCallback } from 'react';
import IUseFeatureFlag from './interfaces/useFeatureFlag';
import {
  FeatureFlagType,
  FeatureFlagVariableType,
  FeatureVariableResultType,
  KameleoonVariationType,
  GetFeatureFlagVariableParamsType,
  VariationType,
  GetVariationParamsType,
  GetVariationsParamsType,
  IsFeatureFlagActiveParamsType,
  SetForcedVariationParametersType,
} from '@kameleoon/javascript-sdk';
import { fetchClientFromContext, useKameleoonContext } from './useKameleoonContext';

/**
 * A React Hook that returns methods related to SDK feature flag
 * and feature experimentation management
 *
 * @returns {IUseFeatureFlag} an object containing the following methods:
 * - `isFeatureFlagActive`,
 * - `getVariation`
 * - `getVariations`
 * - `getFeatureFlagVariationKey`,
 * - `getFeatureFlagVariable`,
 * - `getVisitorFeatureFlags`,
 * - `getActiveFeatureFlags`,
 * - `getFeatureFlags`,
 * - `getFeatureFlagVariables`,
 * - `getEngineTrackingCode`,
 * - `setForcedVariation`,
 * - `evaluateAudiences`
 */
export function useFeatureFlag(): IUseFeatureFlag {
  const context = useKameleoonContext()
  let client = context ? context.kameleoonClient : null;

  const isFeatureFlagActive = useCallback(
    (
      firstParam: string | IsFeatureFlagActiveParamsType,
      secondParam?: string,
    ): boolean => {
      client = fetchClientFromContext(context!);
      if (typeof firstParam === 'string') {
        if (!secondParam) {
          throw new Error(
            '[Kameleoon React SDK Error]: Overload is not used correctly. The second parameter must be provided when the first parameter is a string',
          );
        }

        return client.isFeatureFlagActive(firstParam, secondParam);
      }

      return client.isFeatureFlagActive(firstParam);
    },
    [context],
  );

  const getVariation = useCallback(
    ({
      visitorCode,
      featureKey,
      track,
    }: GetVariationParamsType): VariationType => {
      client = fetchClientFromContext(context!);
      return client.getVariation({ visitorCode, featureKey, track });
    },
    [context],
  );

  const getVariations = useCallback(
    ({
      visitorCode,
      onlyActive,
      track,
    }: GetVariationsParamsType): Map<string, VariationType> => {
      client = fetchClientFromContext(context!);
      return client.getVariations({ visitorCode, onlyActive, track });
    },
    [context],
  );

  const getFeatureFlagVariationKey = useCallback(
    (visitorCode: string, featureKey: string): string => {
      client = fetchClientFromContext(context!);
      return client.getFeatureFlagVariationKey(visitorCode, featureKey);
    },
    [context],
  );

  const getFeatureFlagVariable = useCallback(
    ({
      visitorCode,
      featureKey,
      variableKey,
    }: GetFeatureFlagVariableParamsType): FeatureFlagVariableType => {
      client = fetchClientFromContext(context!);
      return client.getFeatureFlagVariable({
        visitorCode,
        featureKey,
        variableKey,
      });
    },
    [context],
  );

  const getVisitorFeatureFlags = useCallback(
    (visitorCode: string): FeatureFlagType[] => {
      client = fetchClientFromContext(context!);
      return client.getVisitorFeatureFlags(visitorCode);
    },
    [context],
  );

  const getActiveFeatureFlags = useCallback(
    (visitorCode: string): Map<string, KameleoonVariationType> => {
      client = fetchClientFromContext(context!);
      return client.getActiveFeatureFlags(visitorCode);
    },
    [context],
  );

  const getFeatureFlags = useCallback((): FeatureFlagType[] => {
    client = fetchClientFromContext(context!);
    return client.getFeatureFlags();
  }, [context]);

  const getFeatureFlagVariables = useCallback(
    (visitorCode, featureKey): FeatureVariableResultType[] => {
      client = fetchClientFromContext(context!);
      return client.getFeatureFlagVariables(visitorCode, featureKey);
    },
    [context],
  );

  const getEngineTrackingCode = useCallback(
    (visitorCode: string): string => {
      client = fetchClientFromContext(context!);
      return client.getEngineTrackingCode(visitorCode);
    },
    [context],
  );

  const setForcedVariation = useCallback(
    ({
      visitorCode,
      experimentId,
      variationKey,
      forceTargeting,
    }: SetForcedVariationParametersType): void => {
      client = fetchClientFromContext(context!);
      return client.setForcedVariation({
        visitorCode,
        experimentId,
        variationKey,
        forceTargeting,
      });
    },
    [context],
  );

  const evaluateAudiences = useCallback(
    (visitorCode: string): void => {
      client = fetchClientFromContext(context!);
      return client.evaluateAudiences(visitorCode);
    },
    [context],
  );

  return {
    getActiveFeatureFlags,
    getVariation,
    getVariations,
    getFeatureFlags,
    isFeatureFlagActive,
    getEngineTrackingCode,
    getVisitorFeatureFlags,
    getFeatureFlagVariable,
    getFeatureFlagVariables,
    getFeatureFlagVariationKey,
    setForcedVariation,
    evaluateAudiences
  };
}
