import { KameleoonClient, SdkLanguageType } from '@kameleoon/javascript-sdk';
import { SDKParameters } from './types';

/**
 * Creates an instance of KameleoonClient
 * @param parameters {SDKParameters} - parameters of an SDK
 */
export function createClient({
  siteCode,
  configuration,
  externals,
  stubMode,
}: SDKParameters): KameleoonClient {
  const client = new KameleoonClient({
    siteCode,
    configuration,
    externals,
    _internals: {
      packageInfo: {
        type: SdkLanguageType.REACT,
        version: process.env.npm_package_version as string,
        isServer: false,
      },
      prng: externals?.prng,
      platformAnalyzer: externals?.platformAnalyzer,
    },
    stubMode,
  });

  return client;
}
