'use client';
import { useCallback } from 'react';
import IUseInitialize from './interfaces/useInitialize';
import { EventCallbackType, EventType } from '@kameleoon/javascript-sdk';
import { fetchClientFromContext, useKameleoonContext} from './useKameleoonContext';

/**
 * A React Hook that returns methods related to SDK setup and configuration
 *
 * @returns {IUseInitialize} an object containing the following methods:
 * - `initialize`
 * - `isInitialized`
 * - `onEvent`
 */
export function useInitialize(): IUseInitialize {
  const context = useKameleoonContext()
  let client = context ? context.kameleoonClient : null;

  const initialize = useCallback(async (): Promise<boolean> => {
    client = fetchClientFromContext(context!);
    return await client.initialize();
  }, [context]);

  const onEvent = useCallback(
    <E extends EventType>(event: E, callback: EventCallbackType<E>): void => {
      client = fetchClientFromContext(context!);
      client.onEvent(event, callback);
    },
    [context],
  );

  const isInitialized = useCallback((): boolean => {
    client = fetchClientFromContext(context!);
    return client.isInitialized();
  }, [context]);

  return {
    onEvent,
    initialize,
    isInitialized,
  };
}
