'use client';
import { useCallback } from 'react';
import {
  CustomData,
  GetVisitorWarehouseAudienceParamsType,
  JSONType,
  KameleoonDataType,
  RemoteVisitorDataParamsType,
  TrackConversionParamsType,
  FlushParamsType,
} from '@kameleoon/javascript-sdk';
import {
  fetchClientFromContext,
  useKameleoonContext,
} from './useKameleoonContext';
import IUseData from './interfaces/useData';

/**
 * A React Hook that returns methods related to SDK data management
 *
 * @returns {IUseData} an object containing the following methods:
 * - `addData`
 * - `flush`
 * - `trackConversion`
 * - `getRemoteData`
 * - `getRemoteVisitorData`
 * - `getVisitorWarehouseAudience`
 */
export function useData(): IUseData {
  const context = useKameleoonContext();
  let client = context ? context.kameleoonClient : null;

  const addData = useCallback(
    (visitorCode: string, ...data: KameleoonDataType[]): void => {
      client = fetchClientFromContext(context!);
      client.addData(visitorCode, ...data);
    },
    [context],
  );

  const flush = useCallback(
    (params?: string | FlushParamsType): void => {
      client = fetchClientFromContext(context!);
      client.flush(params);
    },
    [context],
  );

  const trackConversion = useCallback(
    ({
      visitorCode,
      goalId,
      revenue,
      negative,
      metadata,
    }: TrackConversionParamsType): void => {
      client = fetchClientFromContext(context!);
      client.trackConversion({
        visitorCode,
        goalId,
        revenue,
        negative,
        metadata,
      });
    },
    [context],
  );

  const getRemoteData = useCallback(
    async (key: string): Promise<JSONType> => {
      client = fetchClientFromContext(context!);
      return await client.getRemoteData(key);
    },
    [context],
  );

  const getRemoteVisitorData = useCallback(
    async (
      params: RemoteVisitorDataParamsType,
    ): Promise<KameleoonDataType[]> => {
      client = fetchClientFromContext(context!);
      return await client.getRemoteVisitorData(params);
    },
    [context],
  );

  const getVisitorWarehouseAudience = useCallback(
    async (
      params: GetVisitorWarehouseAudienceParamsType,
    ): Promise<CustomData | null> => {
      client = fetchClientFromContext(context!);
      return await client.getVisitorWarehouseAudience(params);
    },
    [context],
  );

  return {
    getRemoteData,
    getRemoteVisitorData,
    getVisitorWarehouseAudience,
    trackConversion,
    addData,
    flush,
  };
}
