'use client';
import { useCallback } from 'react';
import IUseVisitorCode from './interfaces/useVisitorCode';
import { fetchClientFromContext, useKameleoonContext} from './useKameleoonContext';

/**
 * A React Hook that returns methods related to visitor code and consent
 *
 * @returns {IUseVisitorCode} an object containing the following methods:
 * - `setLegalConsent`
 * - `getVisitorCode`
 */
export function useVisitorCode(): IUseVisitorCode {
  const context = useKameleoonContext()
  let client = context ? context.kameleoonClient : null;

  const setLegalConsent = useCallback(
    (visitorCode: string, consent: boolean): void => {
      client = fetchClientFromContext(context!);
      client.setLegalConsent(visitorCode, consent);
      client.flush(visitorCode);
    },
    [context],
  );

  const getVisitorCode = useCallback(
    (defaultVisitorCode?: string): string => {
      client = fetchClientFromContext(context!);
      return client.getVisitorCode(defaultVisitorCode);
    },
    [context],
  );

  return {
    setLegalConsent,
    getVisitorCode,
  };
}
