'use client';
import { useContext } from 'react';
import { KameleoonClient } from '@kameleoon/javascript-sdk';
import { KameleoonContext } from './KameleoonContext';
import { createClient } from './createClient';
import { KameleoonContextType } from './types';

/**
 * A React Hook that validates if context is used within a KameleoonProvider and returns it.
 *
 * @returns {KameleoonContextType} an instance of KameleoonContextType
 * @throws Kameleoon SDK hooks must be used within a KameleoonProvider.
 */
export function useKameleoonContext(): KameleoonContextType {
  const context = useContext(KameleoonContext);

  if (!context) {
    throw new Error(
      'Kameleoon SDK hooks must be used within a KameleoonProvider',
    );
  }

  return context;
}

export function fetchClientFromContext(
  context: KameleoonContextType | null,
): KameleoonClient {
  if (!context) {
    throw new Error(
      'Kameleoon SDK hooks must be used within a KameleoonProvider',
    );
  }

  if (!context.kameleoonClient) {
    const sdkParams = context.sdkParameters;

    if (sdkParams) {
      sdkParams.stubMode = false;
      context.kameleoonClient = createClient(sdkParams);
      return context.kameleoonClient;
    }

    throw new Error(
      'Kameleoon SDK hooks must be used within a KameleoonProvider',
    );
  }

  return context.kameleoonClient;
}
