'use client';
import React from 'react';
import { KameleoonContext } from './KameleoonContext';
import { SDKParameters } from './types';

export interface IKameleoonProviderSSRProps {
  /** Child elements of the provider. */
  children: React.ReactNode;
  /** SDK parameters for creating a new instance. */
  sdkParameters: SDKParameters;
}

/**
 * A Provider that wraps the project and provides an access to SDK APIs in SSR mode.
 */
function KameleoonProviderSSR({
  sdkParameters,
  children,
}: IKameleoonProviderSSRProps): React.JSX.Element {
  return (
    <KameleoonContext.Provider
      value={{
        kameleoonClient: null,
        sdkParameters: sdkParameters,
      }}
    >
      {children}
    </KameleoonContext.Provider>
  );
}

export default React.memo(KameleoonProviderSSR);
